import { Calendar, momentLocalizer } from 'react-big-calendar'
import moment from 'moment'
import { Navbar } from '../ui/Navbar'
import 'react-big-calendar/lib/css/react-big-calendar.css'
import { messages } from '../../helpers/calendar-massages-es'
import { CalendarEvent } from './CalendarEvent'
import { useEffect, useState } from 'react'
import { CalendarModal } from './CalendarModal'
import { useDispatch, useSelector } from 'react-redux'
import { uiOpenModal } from '../../actions/ui'
import { eventDesactive, eventSetActive, eventsStartLoading, eventStartAddNew } from '../../actions/events'
import { AddNewFab } from '../ui/AddNewFab'
import { DeleteFab } from '../ui/DeleteFab'
import Swal from 'sweetalert2'

const localizer = momentLocalizer(moment)

export const CalendarScreen = () => {

  const [lastView, setLastView] = useState(localStorage.getItem('lastView') || 'month')
  const dispatch = useDispatch()
  const {events, activeEvent } = useSelector(state => state.calendar)
  const {uid} = useSelector(state => state.auth)

  useEffect(() => {
    dispatch( eventsStartLoading() )
  }, [dispatch])

  const onDoubleClick = () => {
    dispatch( uiOpenModal() )
  }

  const onSelectEvent = (e) => {
    dispatch( eventSetActive(e) )
  }

  const onViewChange = (e) => {
    setLastView(e)
    localStorage.setItem('lastView', e)
  }

  const eventStyleGetter = ( event, start, end, isSelected ) => {
    const style = {
      backgroundColor: (uid===event.user._id ? '#DF9EA4':'#9EDED7'),
      borderRadius: '0px',
      display: 'block',
      color: 'black'
    }
    return {
      style
    }
  }


  const onSelectSlot = (e) => {
    
    if (e.action==='select') {
      
      if( moment().isSameOrAfter(moment(e.start)) ) {
        Swal.fire('Error', 'Fechas de días anteriores','error')
        return;
      }

      const getEvent = async() => {
        let response = await Swal.fire({
          title: '',
          input: 'text',
          inputLabel: 'Título del evento',
          showCancelButton: true,
          inputValidator: (value) => {
            if (!value) {
              return 'Título es obligatorio.'
            }
          }
        });
        // const {value} = await response;
        const title = await response.value;

        if (title) {
          dispatch( eventStartAddNew({
            title,
            start: e.start,
            end:  e.end,
            notes: '',
          }))
        }
        
      }
      getEvent()

    } else {
      dispatch( eventDesactive() )
    }
  }

  return (
    <div className='flex flex-col h-screen'>
      <Navbar />
      <Calendar
        localizer={localizer}
        events={events}
        startAccessor="start"
        endAccessor="end"
        messages={messages}
        eventPropGetter={ eventStyleGetter }
        view={lastView}
        onDoubleClickEvent={ onDoubleClick }
        onSelectEvent={ onSelectEvent }
        onView={onViewChange}
        onSelectSlot={onSelectSlot}
        selectable={true}
        components={{
          event: CalendarEvent
        }}
      />
      <CalendarModal />

      <AddNewFab/>
      {
        !!activeEvent && <DeleteFab/>
      }
      
    </div>
  )
}
