import { useDispatch, useSelector } from 'react-redux'
import { eventStartDelete } from '../../actions/events'


export const DeleteFab = () => {

  const dispatch = useDispatch()
  const {activeEvent} = useSelector(state => state.calendar)


  const handleDelete = () => {
    dispatch( eventStartDelete() )
  }

  return (
    <div 
      className="
        fixed-bottom 
        mb-6 
        ml-6 
        z-10 
        animate__animated 
        animate__rotateIn
        animate__faster
      "
      data-bs-toggle="tooltip" 
      title={`Delete event: ${activeEvent.title}`}
    >
      <div>
        <button
          type="button"
          className="rounded-full bg-[#f70404] hover:bg-[#8b0000] focus:bg-red-200 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out w-10 h-10"
          onClick={handleDelete}
        >
          <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="download" className="w-5 mx-auto" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" >
            <path fill="#fff" d="M404.176 0 256 148.176 107.824 0 0 107.824 148.176 256 0 404.176 107.824 512 256 363.824 404.176 512 512 404.176 363.824 256 512 107.824z"/>
          </svg>
        </button>
      </div>
    </div>
  )
}