import Modal from 'react-modal/lib/components/Modal';
import moment from 'moment';
import DateTime from 'react-datetime'
import "react-datetime/css/react-datetime.css";
import { useEffect, useState } from 'react';
import Swal from 'sweetalert2';
import { useDispatch, useSelector } from 'react-redux';
import { uiCloseModal } from '../../actions/ui';
import { eventDesactive, eventsStartLoading, eventStartAddNew, eventStartUpdated } from '../../actions/events';


const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
  },
}
Modal.setAppElement('#root');

const now = moment().minutes(0).seconds(0).add(1,'hours')

const nowPlus1 = now.clone().add(1,'hours')

const initialEvent = {
  title: '',
  notes: '',
  start: now.toDate(),
  end: nowPlus1.toDate()
}
//! ==========Component============

export const CalendarModal = () => {

  const [dateStart, setDateStart] = useState(now.toDate())
  const [dateEnd, setDateEnd] = useState(nowPlus1.toDate())
  // const [dateStart, setDateStart] = useState(now)
  // const [dateEnd, setDateEnd] = useState(nowPlus1)
  const [titleValid, setTitleValid] = useState(true)

  const dispatch = useDispatch()
  const { modalOpen } = useSelector(state => state.ui)
  const {activeEvent} = useSelector(state => state.calendar)

  const [formValues, setFormValues] = useState(initialEvent)

  const { notes, title, start, end } = formValues

  useEffect(() => {
    if (activeEvent) {
      setFormValues(activeEvent)
    } else {
      setFormValues( initialEvent )
    }
  }, [activeEvent])

  const handleInputChange = ({target}) => {
    setFormValues({
      ...formValues,
      [target.name]: target.value
    })
  }

  const closeModal = () => {
    dispatch( uiCloseModal() )
    dispatch( eventsStartLoading() )
    setFormValues(initialEvent)
  }

  const handleStartDateChange = (e) => {
    setDateStart(e.toDate())
    setFormValues({
      ...formValues,
      start: e.toDate()
    })
  }

  const handleEndDateChange = (e) => {
    setDateEnd(e.toDate())
    setFormValues({
      ...formValues,
      end: e.toDate()
    })
  }
  

  const isValid = (date) => {
    const date2 = moment(date).add(24, 'hours')
    return moment(dateStart).isSameOrBefore(date2)
  };

  const handleSubmitModal = (e) => {
    e.preventDefault()

    if( moment(dateStart).isSameOrAfter(moment(dateEnd)) ) {
      Swal.fire('Error', 'Fecha fin debe ser mayor','error')
      return;
    }

    if( title.trim() === ''){
      setTitleValid(false)
      return 
    }

    if (activeEvent) {
      dispatch( eventStartUpdated( formValues ) )
      dispatch( eventDesactive() )
      
    } else {
      dispatch( eventStartAddNew(formValues) )
    }
    
    setTitleValid(true)
    closeModal()
    // setFormValues(initialEvent)
    // dispatch(uiCloseModal())
  }

  return (
    <Modal
      isOpen={modalOpen}
      // onAfterOpen={afterOpenModal}
      onRequestClose={closeModal}
      closeTimeoutMS={200}
      style={customStyles}
      className={'modal animate__animated animate__fadeIn'}
      overlayClassName={'modal-fondo'}
    >
        <p className='text-lg font-medium'>
          <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="circle" className="w-4 h-4 mr-1 stroke-blue-600 stroke-2 inline-block" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" >
            <path  fill="currentColor" d="M22 4H2v16h10v-2H4V6h16v4h2V4zm-3 13h3v-2h-3v-3h-2v3h-3v2h3v3h2v-3z" />
          </svg>
          {
            activeEvent ? 'Editar evento' : 'Nuevo evento'
          }
        </p>
        <hr className='mb-6 bg-blue-600'/>
        <form onSubmit={handleSubmitModal}>
          <div className="form-group mb-3">
            <label htmlFor="textfechahorainicio" className="form-label inline-block mb-2 text-gray-700" >
              fecha y hora inicio
            </label>
            <DateTime
              onChange={handleStartDateChange}
              value={ start ? start : dateStart }
              inputProps={{
                className: 'picker-fecha',
                readOnly: true
              }}
              isValidDate={ isValid }
              closeOnSelect={true}
            />
          </div>
          <div className="form-group mb-3">
            <label htmlFor="textfechahorafin" className="form-label inline-block mb-2 text-gray-700" >
              fecha y hora fin
            </label>
            <DateTime
              onChange={handleEndDateChange}
              value={end ? end : dateEnd}
              inputProps={{
                className: 'picker-fecha',
                readOnly: true
              }}
              isValidDate={ isValid  }
              closeOnSelect={true}
            />
          </div>
          <div className="form-group mb-3">
            <label htmlFor="tituloynotas" className="form-label inline-block mb-2 text-gray-700" >
              Descripción
            </label>
            <input
              type="text"
              className={`input-title ${ !titleValid && ' invalid' } `}
              placeholder="Título"
              name="title"
              autoComplete="off"
              value={title}
              onChange={handleInputChange}

            />
            <small className="block mt-1 text-xs text-gray-600">
              Descripción corta.
            </small>
          </div>
          <div className="form-group mb-3">
            <textarea
              className=" form-control block w-full px-1 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none resize-none "
              id="exampleFormControlTextarea13"
              rows={5}
              name="notes"
              placeholder="Información adicional"
              value={notes}
              onChange={handleInputChange}
            />
            
          </div>
          <button
            type="submit"
            className=" w-full px-6 py-2.5 bg-blue-600 text-white font-bold leading-tight rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out"
          >
            <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="circle" className="w-5 h-5 mr-2 inline-block" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" >
              <path fill="#9e9e9e" d="M11.506 11.506v488.988h488.988V109.795l-98.289-98.289z"/>
              <g fill="#ecf0f1">
                <path d="M123.685 11.506h264.629v184.09H123.685zM83.416 310.652h345.169v189.843H83.416z" />
              </g>
              <path fill="#2196f3" d="M83.416 310.652h345.169v51.775H83.416z" />
              <path fill="#9e9e9e" d="M255.919 11.506h78.238v132.315h-78.238z" />
              <g fill="#231f20">
                <path d="M508.63 101.659 410.342 3.37C408.184 1.213 405.258 0 402.205 0h-390.7C5.152 0 0 5.152 0 11.506v488.989C0 506.848 5.152 512 11.506 512h488.989c6.353 0 11.506-5.152 11.506-11.506V109.796c-.001-3.053-1.214-5.979-3.371-8.137zM322.656 23.011v109.303h-55.227V23.011h55.227zm-78.239 0V143.82c0 6.353 5.152 11.506 11.506 11.506h78.238c6.353 0 11.506-5.152 11.506-11.506V23.011h31.142V184.09H135.191V23.011h109.226zM94.921 488.989V373.933h322.157v28.043H235.213c-6.353 0-11.506 5.152-11.506 11.506s5.152 11.506 11.506 11.506h181.866v64.002H94.921zm322.158-138.068H94.921v-28.764h322.157v28.764zm71.91 138.068H440.09V310.652c0-6.353-5.152-11.506-11.506-11.506H83.416c-6.353 0-11.506 5.152-11.506 11.506v178.337H23.011V23.011h89.169v172.584c0 6.353 5.152 11.506 11.506 11.506h264.629c6.353 0 11.506-5.152 11.506-11.506V25.392l89.169 89.169v374.428z" />
                <path d="M183.438 401.975h-9.608c-6.353 0-11.506 5.152-11.506 11.506s5.152 11.506 11.506 11.506h9.608c6.353 0 11.506-5.152 11.506-11.506s-5.153-11.506-11.506-11.506z" />
              </g>
            </svg>
            Guardar
          </button>
        </form>
    </Modal>
  )
}
