import { useDispatch } from 'react-redux'
import Swal from 'sweetalert2'
import { startLogin, startRegister } from '../../actions/auth'
import { useForm } from '../../hooks/useForm'
import isEmail from 'validator/lib/isEmail'
import isEmpty from 'validator/lib/isEmpty'
import { useState } from 'react'

export const LoginAndRegisterScreen = () => {
  
  const dispatch = useDispatch()
  const [activeButton, setActiveButton] = useState(false)

  //! Proceso login
  
  const [ formLoginValues, handleLoginInputChange, reset ] = useForm({
    lEmail: '',
    lPassword: ''
  })
  
  const {lEmail,lPassword} = formLoginValues
  
  const handleLogin = (e) => {
    e.preventDefault()
    setActiveButton(true)

    if (!isEmail(lEmail) || isEmpty(lPassword) || lPassword.length <= 5) {
      return Swal.fire('Error','Correo y Contraseña son obligatorios', 'error')
    }
    dispatch(startLogin(lEmail,lPassword))
    setActiveButton(false)
    reset()
  } 

  //! Proceso registro

  const [ formRegisterValues, handleRegisterInputChange ] = useForm({
    rName: "",
    rEmail: "",
    rPassword1: '',
    rPassword2: ''
  })

  const {rName, rEmail, rPassword1, rPassword2} = formRegisterValues



  const handleRegister = (e) => {
    e.preventDefault()
    setActiveButton(true)
    let errors = ''

    if ( isEmpty(rName) ) {
      // return Swal.fire('Error','El nombre es obligatorio', 'error')
      errors += 'El nombre es obligatorio</br>'
    }

    if ( !isEmail(rEmail) ) {
      // return Swal.fire('Error','El correo es obligatorio', 'error')
      errors +='El correo es obligatorio</br>'
    }

    if (rPassword1 !== rPassword2 || rPassword1.length <= 5) {
      // return Swal.fire('Error','La contraseñas deben ser iguales y contener más de seis caracteres', 'error')
      errors +='Las contraseñas deben ser iguales y contener más de seis caracteres'
    }

    if (errors.length > 0) {
      return Swal.fire('Error',errors, 'error')
    } else {
      dispatch( startRegister(rName, rEmail, rPassword1) )
      setActiveButton(false)
    }
  }

  return (
    <div className="flex flex-col sm:flex-row sm:w-screen sm:h-screen justify-center items-center w-full h-full gap-1">
      <div className="block p-6 py-[55px] rounded-lg shadow-2xl bg-gray-50 max-w-md">
        <h3 className='text-center font-semibold text-gray-500 mb-3'>Ingreso</h3>
        <form onSubmit={handleLogin}>
          <div className="form-floating mb-6">
            <input
              type="email"
              className="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none"
              placeholder={'Correo'}
              autoComplete="off"
              name={'lEmail'}
              value={lEmail}
              onChange={handleLoginInputChange}
            />
            <label htmlFor="floatingInput" className="text-gray-500">Correo</label>
          </div>
          <div className="form-floating mb-6">
            <input
              type="password"
              className="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none"
              placeholder={'Contraseña'}
              autoComplete="new-password"
              name={'lPassword'}
              value={lPassword}
              onChange={handleLoginInputChange}
            />
            <label htmlFor="floatingInput" className="text-gray-500">Contraseña</label>
          </div>
          <div className="form-group mb-6">
            <button
              type="submit"
              className="disabled:cursor-not-allowed w-full px-6 py-2.5 bg-blue-600 text-white font-normal leading-tight rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:bg-blue-500 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out"
              disabled={activeButton}
            >
              Ingresar
            </button>
          </div>
        </form>
      </div>

      <div className="block p-6 rounded-lg shadow-2xl bg-blue-600 mb-2 max-w-md">
        <h3 className='text-center text-white font-semibold mb-3'>Registro</h3>
        <form onSubmit={handleRegister}>
          <div className="form-floating mb-3">
            <input
              type="text"
              className="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:outline-blue-600 focus:outline-1"
              autoComplete="off"
              placeholder={'Nombre'}
              name={'rName'}
              value={rName}
              onChange={handleRegisterInputChange}
            />
            <label htmlFor="floatingInput" className="text-gray-500">Nombre</label>
          </div>
          <div className="form-floating mb-3">
            <input
              type="email"
              className="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:outline-blue-600 focus:outline-1"
              placeholder={'Correo'}
              autoComplete="off"
              name={'rEmail'}
              value={rEmail}
              onChange={handleRegisterInputChange}
            />
            <label htmlFor="floatingInput" className="text-gray-500">Correo</label>
          </div>
          <div className="form-floating mb-3">
            <input
              type="password"
              className="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:outline-blue-600 focus:outline-1"
              placeholder="Contraseña"
              autoComplete="new-password"
              name={'rPassword1'}
              value={rPassword1}
              onChange={handleRegisterInputChange}
            />
            <label htmlFor="floatingInput" className="text-gray-500">Contraseña</label>
          </div>
          <div className="form-floating mb-3">
            <input
              type="password"
              className="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:outline-blue-600 focus:outline-1"
              placeholder="Repetir contraseña"
              autoComplete="new-password"
              name={'rPassword2'}
              value={rPassword2}
              onChange={handleRegisterInputChange}
            />
            <label htmlFor="floatingInput" className="text-gray-500">Repetir contraseña</label>
          </div>
          <div className="form-group mb-3">
            <button
              type="submit"
              className="disabled:cursor-not-allowed w-full px-6 py-2.5 bg-white text-gray-700 font-normal leading-tight rounded shadow-md hover:bg-[#CE00ED] hover:shadow-lg hover:text-white focus:bg-[#CE00ED] focus:shadow-lg focus:outline-none focus:ring-0 focus:text-white active:bg-white active:shadow-lg transition duration-150 ease-in-out"
              disabled={activeButton}
            >
              Crear cuenta
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}
