            <path d="M311.908 512H200.092V311.908H0V200.092h200.092V0h111.816v200.092H512v111.816H311.908V512zm-94.161-17.655h76.506V294.253h200.092v-76.506H294.253V17.655h-76.506v200.092H17.655v76.506h200.092v200.092z" />
